<template>
  <div class="product-carousel__wrapper row critical-component-clear">
    <div class="product-carousel__content-wrapper">
      <div class="product-carousel__content">
        <div
          v-if="settings.title !== ''"
          class="product-carousel__title h4"
          v-text="settings.title"
        />

        <p
          v-if="settings.subtitle !== ''"
          class="product-carousel__subtitle h6"
          v-text="settings.subtitle"
        />

        <div class="product-carousel__links-wrapper">
          <template
            v-for="(cta, index) in buttons"
            :key="index"
          >
            <div
              v-if="cta.text"
              class="product-carousel__link text-left"
              :class="{'product-carousel__link--text': cta.style == 'text-link'}"
            >
              <a
                :class="cta.style"
                :href="cta.link"
              >
                <span v-text="cta.text" />
                <icon-arrow-up-right />
              </a>
            </div>
          </template>
        </div>
      </div>
    </div>

    <div class="product-carousel__carousel-wrapper swiper-outer-container">
      <div
        ref="swiperPrev"
        class="swiper-button-prev swiper-button-custom"
        js-product-carousel="prev"
      >
        <icon-chevron-left />
      </div>

      <swiper
        :id="`ProductCarousel-${ liquid.sectionId }`"
        :modules="modules"
        :breakpoints="swiper.breakpoints"
        :prevent-interaction-on-transition="true"
        :navigation="swiper.navigation"
        :observer="true"
        :observe-parents="true"
        :slides-per-view="'auto'"
        :space-between="16"
        :threshold="5"
        class="product-carousel__carousel"
      >
        <swiper-slide
          v-for="(product) in products"
          :key="product.handle"
          class="product-carousel__slide"
          js-product-carousel="slide"
        >
          <product-card
            :collection="collection"
            :product="product"
          />
        </swiper-slide>
      </swiper>

      <div
        ref="swiperNext"
        class="swiper-button-next swiper-button-custom"
        js-product-carousel="next"
      >
        <icon-chevron-right />
      </div>
    </div>

    <div class="product-carousel__links-wrapper mobile">
      <div
        v-for="(cta, index) in buttons"
        :key="`carousel-link-${index}`"
        class="product-carousel__link text-left"
        :class="{'product-carousel__link--text': cta.style == 'text-link'}"
      >
        <a
          :class="cta.style"
          :href="cta.link"
        >
          <span v-text="cta.text" />
          <icon-arrow-up-right />
        </a>
      </div>
    </div>
  </div>
</template>
<script>

/**
 * Vue: Product carousel (product-carousel)
 * -----------------------------------------------------------------------------
 * Section to display a single collection in product cards.
 *
 * @param {String} liquid.sectionId - Section's Liquid ID.
 *
 */
import { Swiper, SwiperSlide } from 'swiper/vue'
import { A11y, Navigation } from 'swiper'

import IconArrowUpRight from '~icons/direction/arrow/up-right.svg'
import IconChevronRight from '~icons/direction/chevron/right.svg'
import IconChevronLeft from '~icons/direction/chevron/left.svg'
import ProductCard from '~async/product-card/product-card'

export default {
  name: 'ProductCarousel',

  components: {
    IconArrowUpRight,
    IconChevronRight,
    IconChevronLeft,
    ProductCard,
    Swiper,
    SwiperSlide,
  },

  props: {
    liquid: {
      type: Object,
      default: () => ({
        sectionId: '',
      }),
    },
    buttons: {
      type: Array,
      default: () => ([]),
    },
    collection: {
      type: Object,
      default: () => ({}),
    },
    products: {
      type: Array,
      default: () => ([]),
    },
    settings: {
      type: Object,
      default: () => ({}),
    },
  },

  setup() {
    return {
      modules: [A11y, Navigation],
    }
  },

  data() {
    return {
      swiper: {
        breakpoints: {
          576: {
            spaceBetween: 16,
          },
          768: {
            spaceBetween: 24,
          },
        },
        navigation: {
          nextEl: '[js-product-carousel="next"]',
          prevEl: '[js-product-carousel="prev"]',
        },
        a11y: {
          prevSlideMessage: window.cnvs.strings.accessibility.carousel.previous,
          nextSlideMessage: window.cnvs.strings.accessibility.carousel.next,
        },
      },
    }
  },

  computed: {
  },

  mounted() {
    if (window.yotpoWidgetsContainer) {
      window.yotpoWidgetsContainer?.initWidgets()
    }
  },

  methods: {
    // Not empty
  },
}
</script>

<style lang="scss">
@import './product-carousel';
</style>
